.rightHeader{
    position: absolute;
    text-align: right;
    z-index: 5;
    top: 10px;
    right: 10px;
    
}
.rightHeader a{
    color: #fffffe;
    text-decoration: none;
    font-size: 24px;
    margin-right: 20px;
}
.rightHeader a:last-child{
    margin-right: 0;
}
.underline:hover{
    /*color: #a786df;*/
    color: #64b5f6;
}
